@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');

a.animated-button:link, a.animated-button:visited {
	position: relative;
	display: block;
	margin: 30px auto 0;
	padding: 14px 15px;
	color: #D24D57;
	font-size: 14px;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	overflow: hidden;
	letter-spacing: .08em;
	border-radius: 0;
	text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
	-webkit-transition: all 1s ease;
	-o-transition: all 1s ease;
	transition: all 1s ease;
}

a.animated-button.selected:link, a.animated-button.selected:visited {
	color: #FFF;
	background-color: #D24D57;
}

a.animated-button.small:link, a.animated-button.small:visited {
	padding: 8px 0;
}

a.animated-button.pulsing:link, a.animated-button.pulsing:visited {
	-webkit-animation: puls 2s linear infinite;
	        animation: puls 2s linear infinite;
}

a.animated-button.inversed:link, a.animated-button.inversed:visited {
	color: #D24D57;
	background-color: rgba(255, 255, 255, 0.56471);
}

a.animated-button:link:after, a.animated-button:visited:after {
	content: "";
	position: absolute;
	height: 0%;
	left: 50%;
	top: 50%;
	width: 150%;
	z-index: -1;
	-webkit-transition: all 0.75s ease 0s;
	-o-transition: all 0.75s ease 0s;
	transition: all 0.75s ease 0s;
}

a.animated-button:link:hover, a.animated-button:visited:hover {
	color: #FFF;
	text-shadow: none;
}

a.animated-button.inversed:link:hover, a.animated-button.inversed:visited:hover {
	color: #D24D57;
}

a.animated-button:link:hover:after, a.animated-button:visited:hover:after {
	height: 450%;
}

a.animated-button.diagonal-sliding:after {
	background: #D24D57;
	-webkit-transform: translateX(-50%) translateY(-50%) rotate(25deg);
	-ms-transform: translateX(-50%) translateY(-50%) rotate(25deg);
	    transform: translateX(-50%) translateY(-50%) rotate(25deg);
}

a.animated-button.inversed.diagonal-sliding:after {
	background: #FFF;
}

@-webkit-keyframes puls {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	15% {
		-webkit-transform: scale(1.03);
		        transform: scale(1.03);
	}
	22% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	30% {
		-webkit-transform: scale(1.06);
		        transform: scale(1.06)
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes puls {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	15% {
		-webkit-transform: scale(1.03);
		        transform: scale(1.03);
	}
	22% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	30% {
		-webkit-transform: scale(1.06);
		        transform: scale(1.06)
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
.fade-enter {
  opacity: 0;
