}
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 800ms ease-in;
  -o-transition: opacity 800ms ease-in;
  transition: opacity 800ms ease-in;
}
.fade-exit {
	display: none;
}
