#slider-landing-text, #slider-landing-image {
    overflow-y: hidden;
    height: 100%;
    width: 100%;
    bottom: 0;
    opacity: 1;
    position: absolute;
    -webkit-transition-property: all;
    -o-transition-property: all;
    transition-property: all;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
    -webkit-transition-timing-function: ease-in-out;
         -o-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
}
#slider-landing-text {
    z-index: 1;
    width: 320px;
    left: calc(50% - 150px);
    padding-top: 20%
}
#slider-landing-text.hidden {
	height: 100px;
	top: 0;
	padding-top: 0;
	pointer-events: none;
}
#slider-landing-image.hidden {
	bottom: 100%;
	opacity: 0;
}
.landing-text {
    width: 300px;
    position: absolute;
    left: calc(50% - 150px);
}
.landing-image {
